@font-face {
  font-family: 'TT Lakes';
  src: local(""), url(./fonts/TTLakes-Bold.woff) format("woff"), url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes';
  src: local(""), url(./fonts/TTLakes-Medium.woff) format("woff"), url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'TT Lakes';
  src: local(""), url(./fonts/TTLakes-Regular.woff) format("woff"), url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-style: normal;
  font-weight: 400; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
  color: #1b1c21; }

body {
  font-family: 'TT Lakes', sans-serif;
  background-color: #f8f8f8; }

a {
  text-decoration: none; }

a:active,
a:hover,
a {
  display: block;
  text-decoration: none;
  color: inherit; }

img {
  display: block; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  width: 40px;
  height: 40px;
  border: 0;
  border-radius: 0;
  background-color: #ffffff;
  cursor: pointer; }

.wrapper {
  padding: 16px;
  margin: 0 auto;
  max-width: 1200px; }
  @media (min-width: 768px) {
    .wrapper {
      padding: 24px; } }

.header {
  display: flex;
  align-items: center;
  margin: -16px -16px 0 -16px;
  padding: 18px 16px;
  background-color: #ffffff;
  border-bottom: 1px solid #d9fff5; }
  @media (min-width: 768px) {
    .header {
      margin: -24px -24px 0 -24px; } }
  @media (min-width: 1120px) {
    .header {
      display: none; } }

.header__menu-button {
  margin-right: 16px; }

.header__logo-image {
  width: 96px; }

.header__logo-link {
  display: inherit; }
  .header__logo-link::before {
    content: '';
    display: none;
    width: 2px;
    height: 32px;
    border-radius: 2px;
    background: #eaeaea;
    margin: auto 20px auto 4px; }
    @media (min-width: 768px) {
      .header__logo-link::before {
        display: block; } }

.header__contacts {
  display: flex;
  margin-left: auto;
  padding: 0; }

.header__contacts-button {
  margin-left: 16px; }

.header__contacts-item {
  display: flex; }

.header__contacts-item--call,
.header__contacts-item--message,
.header__contacts-item--authorization {
  display: none; }
  @media (min-width: 768px) {
    .header__contacts-item--call,
    .header__contacts-item--message,
    .header__contacts-item--authorization {
      display: inherit; } }

.header__contacts-item--order::before {
  content: '';
  display: block;
  width: 2px;
  height: 32px;
  border-radius: 2px;
  background: #eaeaea;
  margin: auto 4px auto 20px; }

.pagination {
  padding: 24px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.pagination__title {
  display: flex;
  margin-bottom: 24px;
  font-size: 28px;
  font-weight: 700;
  line-height: 40px;
  letter-spacing: -0.6px; }
  .pagination__title::before {
    content: '';
    display: inline-block;
    width: 4px;
    height: 40px;
    border-radius: 0 4px 4px 0;
    background: #41f6d7;
    margin: auto 12px auto -16px; }
    @media (min-width: 768px) {
      .pagination__title::before {
        margin-left: -16px; } }

.pagination-contacts {
  display: none; }
  @media (min-width: 1120px) {
    .pagination-contacts {
      display: flex;
      gap: 32px; } }

.pagination__contacts-button {
  display: flex;
  align-items: center;
  min-width: 171px;
  min-width: 171px;
  background-color: #f8f8f8; }

.pagination__contacts-text {
  margin-right: 8px;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px; }

.pagination-list {
  display: flex;
  flex-basis: 100%;
  overflow: overlay; }
  .pagination-list::-webkit-scrollbar {
    width: 0; }

.pagination__link {
  margin: 4px 12px; }

.pagination__item {
  color: #7e7e82;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  white-space: nowrap;
  border: 2px solid transparent; }
  .pagination__item:hover, .pagination__item:focus, .pagination__item:active {
    border: 2px solid #b8ffec;
    border-radius: 6px;
    background-color: #ffffff; }

.about {
  display: flex;
  flex-direction: column; }
  @media (min-width: 768px) {
    .about {
      flex-direction: row;
      gap: 48px; } }
  @media (min-width: 1120px) {
    .about {
      flex-direction: row;
      gap: 36px; } }

.about__text {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  flex-basis: 1; }
  @media (min-width: 768px) {
    .about__text {
      line-height: 20px; }
      .about__text:first-child {
        margin-bottom: 1em; } }

.about__text--tablet {
  display: none; }
  @media (min-width: 768px) {
    .about__text--tablet {
      display: inherit; } }

.about__text--desktop {
  display: none; }
  @media (min-width: 1120px) {
    .about__text--desktop {
      display: inline; } }

.about__button {
  margin: 16px 0;
  width: 133px;
  height: 24px;
  background-color: #f8f8f8; }

.about__button-text {
  font-size: 16px;
  font-weight: 500;
  line-height: 24px; }

.about__button-text::before {
  content: url("/img/about_button_icon.svg");
  margin-right: 8px;
  display: inline-block;
  vertical-align: center;
  width: 24px;
  height: 24px; }

.about__image {
  margin-left: -16px;
  margin-right: -16px;
  width: auto; }
  @media (min-width: 768px) {
    .about__image {
      margin: 0;
      width: 50%;
      border-radius: 7px; } }
